import React from 'react';
import {hot} from "react-hot-loader";
import {getLang} from "@web3game/lang";
import {compose} from "@web3game/common/utils/compose";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {withAPI} from "@web3game/common/components/withAPI";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {BonusInfo} from "@web3game/typings/home";
import {Block} from "../../common/Block";
import {BlockTitle} from "../../common/BlockTitle";
import {SubBlockTitle} from "../../common/SubBlockTitle";
import {Flex} from "../../common/Flex";
import {Splitter} from "../../common/Splitter";

type MyQuoterBonusCardProps = {
    availableTotal?: number;
    bonusList?: BonusInfo[];
    onGainQuoterBonus?: () => Promise<void>;
};

const renderBonusDetail = (bonus: BonusInfo, index: number) => {
    return (
        <Block key={index} withBoxShadow noPadding oneVerticalPadding>
            <Flex>
                <div style={{width: 80, margin: "12px 0 0 0", fontSize: "12px"}}>
                    <span className="textBolder">
                        {bonus.quarterName}
                    </span>
                </div>
                <Splitter />
                <div style={{width: 120}} className="textMainColor textCenter">
                    <span style={{fontSize: 28}}>{bonus.reward}</span>
                    <sub>
                        TTi
                    </sub>
                </div>
                <Splitter />
                <div style={{width: 90, paddingLeft: "5px"}}>
                    <div style={{fontSize: "12px"}}>
                        {getLang().HOME_PAGE.GAIN_DATE}
                    </div>
                    <div style={{fontSize: 12, color: 'gray'}}>
                        {bonus.endday}
                    </div>
                </div>
                <Splitter />
                <div style={{width: 90, paddingLeft: "5px"}}>
                    <div style={{fontSize: "12px"}}>
                        {getLang().HOME_PAGE.AVAILABLE_QUARTER_REWARD}
                    </div>
                    <div style={{fontSize: 12, color: 'gray'}}>
                        {bonus.availableReward} TTi
                    </div>
                </div>
            </Flex>
        </Block>
    );
};

export function MyQuoterBonusCardPure(
    {
        availableTotal = 0,
        bonusList = [],
        onGainQuoterBonus,
    }: MyQuoterBonusCardProps
) {
    return (
        <Block>
            <BlockTitle>
                {getLang().HOME_PAGE.MY_QUARTER_REWARD}
            </BlockTitle>
            <SubBlockTitle buttonText={getLang().GAIN} onButtonClick={onGainQuoterBonus}>
                <span style={{fontSize: "9px"}}>{getLang().HOME_PAGE.THIS_QUOTER_AVAILABLE_BONUS}</span>
                {"  "}
                <span className="textMainColor">
                    {availableTotal} TTi
                </span>
            </SubBlockTitle>
            {bonusList.map(renderBonusDetail)}
        </Block>
    );
}

export const MyQuoterBonusCard = compose<MyQuoterBonusCardProps>(
    hot(module),
    React.memo,
    watchAccountChange,
    withAPI(async ({updateDataVersion}) => {
        const homeAPI = await getHomeAPI();
        const { availableTotal, bonusList } = await homeAPI.getMyBonusResponse();
        const onGainQuoterBonus = async () => {
            try {
                await homeAPI.gainQuoterBonus();
                updateDataVersion();
            } catch (e) {
                alert(e.message);
            }
        };

        return { availableTotal, bonusList, onGainQuoterBonus };
    }),
)(MyQuoterBonusCardPure);
